import React, { Component, Fragment } from 'react'
import { Jumbotron, Container, Button } from 'reactstrap';
import './Hero.scss'
import { Link } from 'react-router-dom';

export default class Home extends Component {
    componentDidMount(){
        
    }
    render() {
        return (
            <Fragment>
                 <Jumbotron fluid className="hero">
                    <Container fluid>
                        <div className="hero-content">
                            <h1 className="display-3">DON'T JUDGE</h1>
                            <h1 className="display-3"> READ IT FIRST!</h1>
                            <Link to="/shop"><Button class="btn" outline color="light">Get your book</Button></Link>
                        </div>
                    </Container>    
                </Jumbotron>
            </Fragment>
        )
    }
}
