import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import AddIcon from '@material-ui/icons/Add';
import { blue } from '@material-ui/core/colors';
import { Fab, TextField, Container, Button } from '@material-ui/core';
import { MuiPickersUtilsProvider,  KeyboardDatePicker } from '@material-ui/pickers';
import MomentUtils from '@date-io/moment';
import { Row, Col, FormGroup, Label, Input, InputGroup, InputGroupAddon, InputGroupText } from 'reactstrap';
import SaveIcon from '@material-ui/icons/Save';
const emails = ['username@gmail.com', 'user02@gmail.com'];
const useStyles = makeStyles({
  avatar: {
    backgroundColor: blue[100],
    color: blue[600],
  },
});

function DialogComponent(props) {
  const { onClose, selectedValue, open } = props;
  const [selectedDate, handleDateChange] = useState(new Date());

  const handleClose = () => {
    onClose(selectedValue);
  };

  const handleListItemClick = (value) => {
    onClose(value);
  };

  return (
    <Dialog onClose={handleClose} aria-labelledby="simple-dialog-title" open={open} >
      <DialogTitle id="simple-dialog-title">Add Promotion</DialogTitle>
      <Container>
       <Row form>
        <Col md={12} style={{padding:10}}>
          <InputGroup>
            <Input type="number" placeholder="Percentage Discount" />
            <InputGroupAddon addonType="append">
            <InputGroupText>%</InputGroupText>
          </InputGroupAddon>
        </InputGroup>
        </Col>
        <Col md={12}>
          <MuiPickersUtilsProvider utils={MomentUtils}>
              <KeyboardDatePicker
              disableToolbar
              variant="inline"
              format="MM/dd/yyyy"
              margin="normal"
              fullWidth 
              label="Expiration Date"
              value={selectedDate}
              onChange={handleDateChange}
              KeyboardButtonProps={{
                'aria-label': 'change date',
              }}
            />
          </MuiPickersUtilsProvider>
        </Col>
        
      </Row>
      <div class="text-center m-5">
          <Button
            variant="contained"
            color="primary"
            size="large"
            onClick={handleClose}
            startIcon={<SaveIcon />}
          >
            Save Promotion
          </Button>
        </div>
      </Container>
    </Dialog>
  );
}

DialogComponent.propTypes = {
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
  selectedValue: PropTypes.string.isRequired,
};

export default function SimpleDialogDemo() {
  const [open, setOpen] = React.useState(false);
  const [selectedValue, setSelectedValue] = React.useState(emails[1]);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = (value) => {
    setOpen(false);
    setSelectedValue(value);
  };

  return (
    <div>
     <div className="text-center m-5" onClick={handleClickOpen}>
          <Fab variant="extended">
            <AddIcon />
            Add Promotion
          </Fab>
        </div>
      <DialogComponent selectedValue={selectedValue} open={open} onClose={handleClose} />
    </div>
  );
}
