import React, { Component, Fragment, useState } from 'react'
import { Container, Button, Input, InputGroupAddon, InputGroupText, InputGroup } from 'reactstrap';
import CartItems from '../../../components/CartItems/CartItems';
import './Cart.scss';
import { Link } from 'react-router-dom';
import {useSelector} from 'react-redux';
import {removeItem} from './../../../actions/';
import {useDispatch} from 'react-redux';


export default function Cart(props) {
  const isAuth = useSelector(state => state.authReducer);
    const[promoCode] = useState(5);
    const dispatch = useDispatch();

 

    const cartItems = useSelector(state => state.cartReducer)
    console.log(cartItems)
    
    const calcSum = () => {
        let total = 0
        cartItems.map(item => {
            total+=item.price
        })
        return total - promoCode
        
    }

    const [items, setItem] = useState([])

    const setQty = (qty) => {
      setItem(prevState => [...prevState,qty])
    }
        return (
            <Container>
                <div className="row mt-5">
                    <div className="col-md-8">
                         <div style={{background:"#A9A9A9", padding:2}}>
                             <h3>Your Cart {cartItems.length}</h3>
                        </div>
                        <div style={{marginTop:100}} className="container">
                           {cartItems.map((item,index) => <CartItems getQty={setQty} removeItem={() => dispatch(removeItem(index))} id={item.id} title={item.title} edition={item.edition} price={item.price} book_id={item.id} /> )}
                        </div>
                    </div>
                    <div className="col-md-4 order-md-2 mb-4">
      <h4 className="d-flex justify-content-between align-items-center mb-3">
        <span className="text-muted">Your cart</span>
        <span className="badge badge-secondary badge-pill">{cartItems.length}</span>
      </h4>
      <ul className="list-group mb-3">
          {cartItems.map(item =>{
               return <li className="list-group-item d-flex justify-content-between lh-condensed">
               <div>
          <h6 className="my-0">{item.name}</h6>
          <small className="text-muted">{item.desc}</small>
               </div>
          <span className="text-muted">${item.price}</span>
             </li>
          })}       
        <li className="list-group-item d-flex justify-content-between bg-light">
          <div className="text-success">
        <h6 className="my-0">Promo code</h6>
            <small>EXAMPLECODE</small>
          </div>
        <span className="text-success">-${promoCode}</span>
        </li>
        <li className="list-group-item d-flex justify-content-between">
          <span>Total (USD)</span>
        <strong>${calcSum()}</strong>
        </li>
      </ul>
            <form className="card p-2">
                <div className="input-group">
                    <input type="text" className="form-control" placeholder="Promo code"/>
                    <div className="input-group-append">
                    <button type="submit" className="btn btn-secondary">Redeem</button>
                    </div>
                </div>
                </form>
              {!isAuth?
              <Button onClick={() => props.history.push('/login')} outline color="secondary" className="w-100">Checkout</Button> 
              :
               <Button onClick={() => props.history.push('/checkout',{items})} outline color="secondary" className="w-100">Checkout</Button>
                }
                </div>
                </div>
               
            </Container>
        )
    
}
