import React, { Component } from 'react';
import axios from 'axios';
import Swal from 'sweetalert2';
import {emptyCart} from './../../../actions';


export default class Checkout extends Component {
  state = {
    parishes: [
      "Hanover",		
      "St.Elizabeth",	
      "St.James	",
      "Trelawny",
      "Westmoreland",	
      "Clarendon"	,
      "Manchester",	
      "St.Ann",
      "St.Catherine",
      "St.Mary",
      "Kingston", 
      "Portland",	
      "St.Andrew",
      "St.Thomas"
      ],
      selectedCountry: ""
  }
  handleSubmit = (e) => {
    e.preventDefault();
    const newUser= {
        name:this.state.name,
        email: this.state.email,
        address: this.state.address,
        password: this.state.password
    }
    this.setState({newUser}, () => console.log(this.state.newUser))

   
}

purchaseItem = (items) => {
  axios.post("https://glacial-fortress-92435.herokuapp.com/users/checkout",items)
  .then(resp => {
    Swal.fire({
      title: 'Order has been placed!',
      html: 'You will be redirected to the home page',
      timer: 2000,
      timerProgressBar: true,
      onBeforeOpen: () => {
        Swal.showLoading()
        this.timerInterval = setInterval(() => {
          const content = Swal.getContent()
          if (content) {
            const b = content.querySelector('b')
            if (b) {
              b.textContent = Swal.getTimerLeft()
            }
          }
        }, 100)
      },
      onClose: () => {
        this.props.history.push('/shop')
        window.location.reload()

      }
    }).then((result) => {
      /* Read more about handling dismissals below */
      if (result.dismiss === Swal.DismissReason.timer) {
        console.log('I was closed by the timer')
      }
    })
  })
}


  componentDidMount(){
    axios.get('https://restcountries.eu/rest/v2/all')
    .then(resp => this.setState({countries:resp.data}))

    console.log(this.props.history.location)
  }
    render() {
        return (
            <div className="bg-light">
<div className="container">
  <div className=" text-center">
    <img className="d-block mx-auto mb-4" src="https://getbootstrap.com/assets/brand/bootstrap-solid.svg" alt="" width="72" height="72"/>
    <h2>Checkout</h2>
  </div>

  <div className="row">
    
    <div className="col-md-8 offset-md-2 order-md-1">
      <h4 className="mb-3">Billing address</h4>
      <form className="needs-validation" novalidate>
        <div className="row">
          <div className="col-md-6 mb-3">
            <label for="firstName">First name</label>
            <input type="text" className="form-control" id="firstName" placeholder=""  required/>
            <div className="invalid-feedback">
              Valid first name is required.
            </div>
          </div>
          <div className="col-md-6 mb-3">
            <label for="lastName">Last name</label>
            <input type="text" className="form-control" id="lastName" placeholder=""  required/>
            <div className="invalid-feedback">
              Valid last name is required.
            </div>
          </div>
        </div>


        <div className="mb-3">
          <label for="email">Email <span className="text-muted">(Optional)</span></label>
          <input type="email" className="form-control" id="email" placeholder="you@example.com"/>
          <div className="invalid-feedback">
            Please enter a valid email address for shipping updates.
          </div>
        </div>

        <div className="mb-3">
          <label for="address">Address</label>
          <input type="text" className="form-control" id="address" placeholder="1234 Main St" required/>
          <div className="invalid-feedback">
            Please enter your shipping address.
          </div>
        </div>

        <div className="mb-3">
          <label for="address2">Address 2 <span className="text-muted">(Optional)</span></label>
          <input type="text" className="form-control" id="address2" placeholder="Apartment or suite"/>
        </div>

        <div className="row">
          <div className="col-md-5 mb-3">
            <label for="country">Country</label>
            <select onChange={(e) => this.setState({selectedCountry:e.target.value})} className="custom-select d-block w-100" id="country" required>
              <option value="">Choose...</option>
              {this.state.countries.map( country => <option>{country.name}</option>)}
            </select>
            <div className="invalid-feedback">
              Please select a valid country.
            </div>
          </div>
          {this.state.selectedCountry === "Jamaica"?
          <div className="col-md-4 mb-3">
            <label for="state">State/Parish</label>
            <select className="custom-select d-block w-100" id="state" required>
              <option value="">Choose...</option>
              {this.state.parishes.map(parish => <option>{parish}</option>)}
            </select>
            <div className="invalid-feedback">
              Please provide a valid state.
            </div>
          </div>
          : 
          ""
          }
          <div className="col-md-3 mb-3">
            <label for="zip">Zip</label>
            <input type="text" className="form-control" id="zip" placeholder="" required/>
            <div className="invalid-feedback">
              Zip code required.
            </div>
          </div>
        </div>
        <hr className="mb-4"/>
        <div className="custom-control custom-checkbox">
          <input type="checkbox" className="custom-control-input" id="save-info"/>
          <label className="custom-control-label" for="save-info">Save this information for next time</label>
        </div>
        <hr className="mb-4"/>

        <h4 className="mb-3">Payment</h4>

        <div className="d-block my-3">
          <div className="custom-control custom-radio">
            <input id="credit" name="paymentMethod" type="radio" className="custom-control-input" checked required/>
            <label className="custom-control-label" for="credit">Credit card</label>
          </div>
          <div className="custom-control custom-radio">
            <input id="debit" name="paymentMethod" type="radio" className="custom-control-input" required/>
            <label className="custom-control-label" for="debit">Debit card</label>
          </div>
          <div className="custom-control custom-radio">
            <input id="paypal" name="paymentMethod" type="radio" className="custom-control-input" required />
            <label className="custom-control-label" for="paypal">Paypal</label>
          </div>
        </div>
        <div className="row">
          <div className="col-md-6 mb-3">
            <label for="cc-name">Name on card</label>
            <input type="text" className="form-control" id="cc-name" placeholder="" required/>
            <small className="text-muted">Full name as displayed on card</small>
            <div className="invalid-feedback">
              Name on card is required
            </div>
          </div>
          <div className="col-md-6 mb-3">
            <label for="cc-number">Credit card number</label>
            <input type="text" className="form-control" id="cc-number" placeholder="" required/>
            <div className="invalid-feedback">
              Credit card number is required
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-md-3 mb-3">
            <label for="cc-expiration">Expiration</label>
            <input type="text" className="form-control" id="cc-expiration" placeholder="" required/>
            <div className="invalid-feedback">
              Expiration date required
            </div>
          </div>
          <div className="col-md-3 mb-3">
            <label for="cc-expiration">CVV</label>
            <input type="text" className="form-control" id="cc-cvv" placeholder="" required/>
            <div className="invalid-feedback">
              Security code required
            </div>
          </div>
        </div>
        <hr className="mb-4"/>
        <button onClick={() => this.purchaseItem(this.props.history.location.state)} className="btn btn-primary btn-lg btn-block" type="submit">Continue to checkout</button>
      </form>
    </div>
  </div>

  <footer className="my-5 pt-5 text-muted text-center text-small">
    <p className="mb-1">&copy; 2017-2018 Company Name</p>
    <ul className="list-inline">
      <li className="list-inline-item"><a href="#">Privacy</a></li>
      <li className="list-inline-item"><a href="#">Terms</a></li>
      <li className="list-inline-item"><a href="#">Support</a></li>
    </ul>
  </footer>
</div>
            </div>
        )
    }
}
