import React, { Component } from 'react'
import { InputGroup, InputGroupAddon, Input, Button, Container } from 'reactstrap'
import BooksView from '../../../components/BooksView/BooksView'

export default class Shop extends Component {
    render() {
        return (
            <div className="mt-5">
              <Container>
                 <InputGroup>
                    <Input placeholder="Search for a book" />
                    <InputGroupAddon addonType="append">
                      <Button color="secondary">Search</Button>
                    </InputGroupAddon>
                  </InputGroup>
                  <BooksView />
              </Container>
            </div>
        )
    }
}
