import React, { Component } from 'react';
import './Register.scss';
import { Input } from 'reactstrap';
import axios from 'axios';
import Swal from 'sweetalert2';
export default class Login extends Component {
    state = {
        name:"",
        email:"",
        username:"",
        address:"",
        password:"",
        retype:"",
        newUser:{}
    }

    setValues = (e) => { 
        const toChange = e.target.id;
        let obj = {};
        obj[toChange] = e.target.value
        this.setState(obj)
    }

   handleSubmit = (e) => {
        e.preventDefault();
        const newUser= {
            name:this.state.name,
            username:this.state.username,
            email: this.state.email,
            password: this.state.password,
            confirm_password:this.state.retype
        }
        this.setState({newUser},
             () => axios.post("https://glacial-fortress-92435.herokuapp.com/users",this.state.newUser).
             then(resp => {
                Swal.fire({
                    icon: 'success',
                    title: 'Account created',
                  })
                  this.props.history.push('/login')
             }))
       
    }
    render() {
        return (
            <div>
                <div class="container">
                    <div class="row">
                    <div class="col-sm-9 col-md-7 col-lg-5 mx-auto">
                        <div class="card card-signin my-5">
                        <div class="card-body">
                            <h5 class="card-title text-center">Register</h5>
                            <form class="form-signin">
                            <div class="form-label-group">
                                <Input onChange={(e) => this.setValues(e)} type="text" id="name" class="form-control" placeholder="Full Name" defaultValue={this.state.name} required autofocus/>
                                <label for="name">Full Name</label>
                            </div>
                            <div class="form-label-group">
                                <Input onChange={(e) => this.setValues(e)} type="text" id="username" class="form-control" placeholder="Username" defaultValue={this.state.username} required autofocus/>
                                <label for="username">Username</label>
                            </div>

                            <div class="form-label-group">
                                <Input onChange={(e) => this.setValues(e)} defaultValue={this.state.email} type="text" id="email" class="form-control" placeholder="Email" required/>
                                <label for="email">Email Address</label>
                            </div>
                            <div class="form-label-group">
                                <Input onChange={(e) => this.setValues(e)} defaultValue={this.state.address} type="text" id="address" class="form-control" placeholder="Postal Address" required autofocus/>
                                <label for="address">Postal Address</label>
                            </div>

                            <div class="form-label-group">
                                <Input onChange={(e) => this.setValues(e)} defaultValue={this.state.password} type="password" id="password" class="form-control" placeholder="Password" required/>
                                <label for="password">Password</label>
                            </div>
                            <div class="form-label-group">
                                <Input onChange={(e) => this.setValues(e)}  defaultValue={this.state.retype} type="password" id="retype" class="form-control" placeholder="Retype Password" required invalid={this.state.password!==this.state.retype} autofocus/>
                                <label for="retype">Retype Password</label>
                            </div>

                            {/* <div class="custom-control custom-checkbox mb-3">
                                <input type="checkbox" class="custom-control-input" id="customCheck1" />
                                <label class="custom-control-label" for="customCheck1">Remember password</label>
                            </div> */}
                            <button class="btn btn-lg btn-secondary btn-block text-uppercase" type="submit" onClick={(e) => this.handleSubmit(e)}>Create Account</button>
                            </form>
                        </div>
                        </div>
                    </div>
                    </div>
                </div>
            </div>
        )
    }
}
