import React, { Component, useState } from 'react'
import Book from '../Book/Book';
import './BookView.scss';
import bookImage from './../../assets/img/bookImage.jpg';
import axios from 'axios';


export default class BooksView extends Component {
  state = {
    books:[]
  }

  componentDidMount(){
    axios.get("https://glacial-fortress-92435.herokuapp.com/books")
    .then(resp => this.setState({books: resp.data.data}))
  }
  
  render(){
    return (
            <div className="row book-view">
                {this.state.books.map(book => {
                    return <div className="col-md-3">
                        <Book book={book} descr={book.descr} title={book.title} price={book.price} bookSrc={book.src} />
                    </div> 
                })}
            </div>
        )
  }
}
