import React, { Fragment, useEffect, useState } from 'react';
import NavbarComponent from './components/Navbar/Navbar';
import Home from './pages/CustomerViews/Home/Home';
import Shop from './pages/CustomerViews/Shop/Shop';
import Register from './pages/CustomerViews/Register/Register';
import Login from './pages/Login/Login';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import {createBrowserHistory} from 'history';
import BookView from './components/BookView/BookView';
import Cart from './pages/CustomerViews/Cart/Cart';
import Checkout from './pages/CustomerViews/Checkout/Checkout';
import Inventory from './pages/AdminViews/Inventory/Inventory';
import {useSelector} from 'react-redux';

const history = createBrowserHistory();


export default function App() {
    const [isAdmin,setIsAdmin] = useState(false);
    const isAuth = useSelector(state => state.authReducer);
        
    const getUsername = (name) => {
        name==="rbrown23"?setIsAdmin(true):setIsAdmin(false)
    }
    return (
        <Router history={history}>
            <NavbarComponent history={history} /> 
            {isAuth?
                <Switch>
                    {
                    isAdmin?
                    <Route path="/" exact component={Inventory } />
                    :
                    <Route path="/" exact component={Home } />
                    }
                    <Route path="/shop" component={Shop} />
                    <Route path="/register" component={Register} />
                    <Route path="/book" component={BookView} />
                    <Route path="/cart" component={Cart} />
                    <Route path="/checkout" component={Checkout} />
                </Switch>
                :
                <Switch>
                    <Route path="/login" render={(props => <Login {...props} getUsername={getUsername} />)} /> 
                    <Route path="/shop" component={Shop} />
                    <Route path="/register" component={Register} />
                    <Route path="/book" component={BookView} />
                    <Route path="/cart" component={Cart} />
                </Switch>
                }
        </Router>
    )
}
