import React, { useState, useEffect } from 'react'
import { Button, Container } from 'reactstrap'
import product from './../../assets/img/bookImage.jpg';
import './Bookview.scss';
import {useDispatch} from 'react-redux';
import {addItem} from './../../actions/';


export default function BookView(history) {
    const dispatch = useDispatch();
    const [qty, setQty] = useState(1);

    const changeVal = (val) => {
        setQty(val)
    }

    return (
        <Container>
            <div className="row">
                <div className="image col-md-6 col-lg-6 col-xs-12">
                    <img className="h-100 w-100" src={product} alt="Product"/>
                </div>
                <div className="prod-details col-md-6 col-lg-6 col-xs-12">
                <h3>{history.location.state.book.title}</h3><small>Edit</small>
                    <p>{history.location.state.book.edition}</p><small>Edit</small>
                    <p style={{color:"#007600"}}>Currently in Stock</p>
                    <hr/>
                    <h4>${history.location.state.book.price}.00</h4>
                    <label for="exampleFormControlSelect1">Qty</label>
                    <select defaultValue={qty} onChange={(e) => changeVal(e.target.value)} class="form-control w-25">
                        {[1,2,3,4,5,6,7,8,9].map(i => <option>{i}</option>)}
                    </select>
                    <hr />
                   <Button onClick={() => dispatch(addItem(history.location.state.book))} className="cart-icon" ><i className="fas fa-shopping-cart"></i> Add to Cart</Button>
                </div>
            </div>
            </Container>
    )
}
