const cartReducer = (state=[], action) => {
    switch(action.type){
        case "ADD ITEM":
            let newArray = state.slice()
            newArray.splice(action.payload.id, 0, action.payload)
            console.log(newArray);
            return newArray;

            
        
        case "REMOVE ITEM":
            let cartArray= state.slice();
            cartArray.splice(action.index, 1)
            return cartArray;

        case "EMPTY CART":
            return state;
        default:
            return state;
    } 

}

export default cartReducer;