import React, { Component, useState } from 'react';
import MaterialTable from 'material-table';
import { Container } from 'reactstrap';
import DialogComponent from './../../../components/Dialog/DialogComponent';
import axios from 'axios';
import { useEffect } from 'react';
export default function Inventory() {

  const sendNewBook = (newBook) => {
    const modifiedBook = {};
    modifiedBook.id = 1;
    modifiedBook.title = newBook.title;
    modifiedBook.author = newBook.author;
    modifiedBook.price = newBook.price;
    modifiedBook.stock = newBook.stock;
    modifiedBook.reorder_amount = newBook.reorder;
    axios.post("https://glacial-fortress-92435.herokuapp.com/books",modifiedBook)
    .then(resp => console.log(resp.data))
  }

  const sendupdatedBook = (id,updatedBook) => {
    const modifiedBook = {};
    modifiedBook.id = 1;
    modifiedBook.title = updatedBook.title;
    modifiedBook.author = updatedBook.author;
    modifiedBook.price = updatedBook.price;
    modifiedBook.stock = updatedBook.stock;
    modifiedBook.reorder_amount = updatedBook.reorder;
    axios.put(`https://glacial-fortress-92435.herokuapp.com/books/${id}`,modifiedBook)
    .then(resp => console.log(resp.data))
  }




  useEffect(() => {
    axios.get("https://glacial-fortress-92435.herokuapp.com/books")
    .then(resp => {
      const dbBooks = resp.data.data
      setState((prevState) => {
        const data = dbBooks;
        return { ...prevState, data };
      })
    })
  },[])
    const [state, setState] = useState({
        coloumns:[
            { title: "ID", field: "id", type:"number" },
            { title: "Title", field: "title" },
            { title: "Author", field: "author" },
            { title: "Price", field: "price", type: "numeric"},
            {title: "Stock", field:"stock", type:"numeric"},
            {title: "Reorder Threshold", field: "reorder_amount"},
          ],

          data: [],
        actions: [
            {
              icon: 'create',
              tooltip: 'Edit Book',
              onClick: (event, rowData) => {
                // Do save operation
              }
            },
            {
                icon: 'delete',
                tooltip: 'Delete Book',
                onClick: (event, rowData) => {

                }
              }
          ]

    })

        return (
            <Container style={{marginTop:100}}>

              <DialogComponent />
                <MaterialTable
                 columns={state.coloumns}
                  data={state.data}
                  title="Book Inventory"
                //   detailPanel={rowData => <BookView />}
                //   actions={this.state.actions}
                  editable={{
                    onRowAdd: (newData) =>
                      new Promise((resolve) => {
                        setTimeout(() => {
                          resolve();
                          setState((prevState) => {
                            const data = [...prevState.data];
                            data.push(newData);
                            return { ...prevState, data };
                          });
                          sendNewBook(newData);
                        }, 600);
                      }),
                    onRowUpdate: (newData, oldData) =>
                      new Promise((resolve) => {
                        setTimeout(() => {
                          resolve();
                          if (oldData) {
                            setState((prevState) => {
                              const data = [...prevState.data];
                              data[data.indexOf(oldData)] = newData;
                              return { ...prevState, data };
                            });
                            sendupdatedBook(newData.id,newData)
                          }
                        }, 600);
                      }),
                    onRowDelete: (oldData) =>
                      new Promise((resolve) => {
                        setTimeout(() => {
                          resolve();
                          setState((prevState) => {
                            const data = [...prevState.data];
                            data.splice(data.indexOf(oldData), 1);
                            return { ...prevState, data };
                          });
                        }, 600);
                      }),
                  }}  
                  />                
            </Container>
        )
  
}
