import React, { useState } from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  NavbarText,
  Button
} from 'reactstrap';
import {Link} from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import {logoutUser} from './../../actions';


import './Navbar.scss';

export default function NavbarComponent(props) {
  const [isOpen, setIsOpen] = useState(false);
  const isAuth = useSelector(state => state.authReducer);
  const cartItems = useSelector(state => state.cartReducer);
  
  const dispatch = useDispatch();
  
  const logoutHandle = () => {
    dispatch(logoutUser())
   props.history.push('/login')
    window.location.reload()
  }


  const toggle = () => setIsOpen(!isOpen);
  
  return (
    <div>
      <Navbar className="nav-bg" light expand="md">
        <NavbarBrand href="/">Book Store</NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="mr-auto" navbar>
            <NavItem>
              <NavLink className="link"><Link  className="route" to="/">Home</Link></NavLink>
            </NavItem>
            <NavItem>
              <NavLink  className="link"><Link className="route"  to="/shop">Shop</Link></NavLink>
            </NavItem>
            <NavItem>
              <NavLink  className="link"><Link className="route"  to="/cart">Cart ({cartItems.length}) </Link></NavLink>
            </NavItem>
           {isAuth?
            <NavItem>
              <NavLink  className="link"><Link className="route"  to="/checkout">Checkout</Link></NavLink>
            </NavItem>
            :""
            }
          </Nav>
        {isAuth?
          <>
          <p className="font-weight-bold mr-5" onClick={console.log(props.history)}>Welcome, {localStorage.getItem("username")} </p>
          <Button onClick={() =>{logoutHandle(); localStorage.clear()}} className="btn login" outline>Logout</Button>
          </>
         :
         <Link to="/login" className="login"><Button className="btn" outline>Login</Button></Link>
          }
        </Collapse>
      </Navbar>
    </div>
  );
}
