export const addItem = (newBook) => {
    return{
        type: "ADD ITEM",
        payload:newBook
    }
}

export const removeItem = (index) =>{
    return{
        type: "REMOVE ITEM",
        paylod: index
    }
}
export const emptyCart = (index) =>{
    return{
        type: "EMPTY CART",
    }
}

export const authenticateUser = () => {
    return{
        type:"IS_AUTHENTICATED"
    }
}

export const logoutUser = () => {
    return{
        type:"IS_LOGGEDOUT"
    }
}

export const setUser = (user) => {
    return{
        type:"SET_USER",
        payload:user
    }
}

export const delUser = (user) => {
    return{
        type:"DELETE_USER",
        payload:user
    }
}