import React, { Component } from 'react';
import book from './../../assets/img/bookImage.jpg';
import { Button, Input } from 'reactstrap';


export default class CartItems extends Component {
    state = {
        selectToggle: true,
        qty: 1,
        item:{}
    }

    populateItems = (item) =>{
        // this.setState(prevState => ({
        //     items: [...prevState.items,item]
        // }))

        this.state.item = {...item};

    }
    render() {
        return (
            <div className="mt-5" onLoad={() => this.populateItems({book_id:this.props.book_id,quantity:this.state.qty})}>
                <div className="row">
                    <div className="col-md-2">
                        <img className="w-100 h-100" src={book} alt="prod" />
                    </div>
                    <div className="col-md-8">
                        <h5>{this.props.title}</h5>
                        <p>{this.props.edition}</p>
                        {this.state.selectToggle?
                            <p>Qty: {this.state.qty}</p>
                            :
                            <div>
                                <label for="exampleFormControlSelect1">Qty</label>
                                <select value={this.state.qty} class="form-control w-25" onChange={(e) => this.setState({qty: parseInt(e.target.value)})}>
                                    {[1,2,3,4,5,6,7,8,9].map(i => <option>{i}</option>)}
                                </select>
                            </div>
                        }  

                        <div className="mt-3"> 
                            <Button onClick={() => this.setState({selectToggle: !this.state.selectToggle})} color="warning" className="w-25">Edit</Button>
                            {
                                this.state.selectToggle?
                                <Button onClick={this.props.removeItem} color="danger" className="ml-4 w-25">Remove</Button>
                                :
                                <Button onClick={() =>{this.setState({selectToggle: !this.state.selectToggle});this.props.getQty({book_id:this.props.book_id,quantity:this.state.qty});this.populateItems({book_id:this.props.book_id,quantity:this.state.qty})}} color="success" className="ml-4 w-25">Done</Button>
                            }
                        </div>
                    </div>
                    <div className="col-md-2">
                        <h5 style={{color: "#00ccff"}}>${this.props.price}</h5>
                    </div>
                </div>
            </div>
        )
    }
}
