
import React, { useState } from 'react';
import {
  Card, CardImg, CardText, CardBody,Button
} from 'reactstrap';
import './Book.scss';
import { useHistory } from 'react-router-dom';
import {useDispatch} from 'react-redux';
import {addItem} from './../../actions/';

const Book = (props) => {
  const dispatch = useDispatch();
  const history = useHistory();

  const handleView = (book,e) => {
    e.preventDefault();
    history.push("/book",{book})
  }
  return (
    <div>
      <Card className="card">
        <CardImg top style={{height:300}} width="50%" src={props.bookSrc} alt="Card image cap" />
        <CardBody>
          <h5>{props.title} </h5>
          <h6>${props.price}</h6>
            <CardText>{props.descr}</CardText>
              <Button onClick={() => dispatch(addItem(props.book))} className="cart-icon w-100"><i className="fas fa-shopping-cart"></i> Add to Cart</Button>
              {/* <Link style={{textDecoration:"none", color:"#000"}} to="/book"> */}
                <Button onClick={(e) => handleView(props.book,e)} color="success" outline className="mt-5 w-100" href="/"><i className="fas fa-eye"></i> View Product</Button>
              {/* </Link> */}
        </CardBody>
      </Card>
    </div>
  );
};

export default Book;