import React, {  useState, useEffect } from 'react';
import './Login.scss';
import { Link } from 'react-router-dom';
import { Input, Button } from 'reactstrap';
import {useDispatch} from 'react-redux';
import {authenticateUser, setUser} from './../../actions';
import axios from 'axios';
import Swal from 'sweetalert2';


export default function Login(props) {
    
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");
    const [isValid, setIsValid] = useState(false);
    const [incorrect, setIncorrect] = useState(-1);

    const dispath = useDispatch();

const [user, setUserInfo] = useState({});

const validateUser = () => {
    if(incorrect > -1){
        dispath(authenticateUser())
        props.history.push('/',{user})
        console.log(props.history)
    }else{
       
    }
}

        useEffect(() => {
            validateUser()
        },[incorrect])

    async function handleLogin(e) {
        e.preventDefault();
        let resp = await axios.post(
            "https://glacial-fortress-92435.herokuapp.com/users/login",
            {username,password})
        
        setUserInfo(resp.data.data.user)
        setIncorrect(resp.data.code)
        localStorage.setItem("token",resp.data.data.token)
        localStorage.setItem("username",resp.data.data.user.username)
    }
        return (
            <div>
                <div class="container">
                    <div class="row">
                    <div class="col-sm-9 col-md-7 col-lg-5 mx-auto">
                        <div class="card card-signin my-5">
                        <div class="card-body">
                            <h5 class="card-title text-center">Sign In</h5>
                            {incorrect <= -1? <p style={{color:"red", fontWeight:"700"}}>Incorrect username or password entered</p>:""}
                            <form class="form-signin">
                            <div class="form-label-group">
                                <Input 
                                onChange={(e) => setUsername(e.target.value) } 
                                defaultValue={username} 
                                invalid={isValid}
                                type="text" 
                                id="username" 
                                class="form-control" 
                                placeholder="Username" 
                                required 
                                />
                                <label for="username">Username</label>
                            </div>

                            <div class="form-label-group">
                                <Input 
                                defaultValue={password} 
                                onChange={(e) => setPassword(e.target.value)} 
                                invalid={isValid} 
                                type="password" 
                                class="form-control"
                                placeholder="Password"
                                required
                                id="inputPassword"
                                />
                                <label for="inputPassword">Password</label>
                            </div>


                            <Button onClick={(e) => {handleLogin(e); props.getUsername(username)}} className="btn btn-lg btn-secondary btn-block text-uppercase" type="submit">Sign in</Button>
                            <Link style={{textDecoration:"none"}} to="/register"><button class="btn btn-lg btn-block mt-3 register text-uppercase" type="submit">Register</button></Link>
                            </form>
                        </div>
                        </div>
                    </div>
                    </div>
                </div>
            </div>
        )
    }

